.mode-toggle{
    cursor: pointer;
    position: fixed;
    top: 25px;
    right: 25px;
    z-index: 15;
    width: 45px;
    height: 25px;
    overflow: visible;
    border: 2px solid var(--p-color);
    border-radius: 20px;
    background-color: var(--body-bg-color);
    animation: image-appear 1s ease;

    &, 
    *{
        user-select: none;
        -webkit-tap-highlight-color: transparent;
    }
    .circle{
        cursor: pointer;
        display: table;
        position: absolute;
        top: -5px;
        left: -5px;
        z-index: 15;
        width: 32px;
        height: 32px;
        text-align: center;
        white-space: nowrap;
        border-radius: 50%;
        background-color: var(--modetogglecircle-bg-color);
        transition: transform 0.25s ease-in-out;

        .fas.fa-moon,
        .fas.fa-sun{
            display: table-cell;
            vertical-align: middle;
            text-align: center;
            color: var(--modetogglemoon-color);
        }
    }
}
.mode-dark{
    background-color: var(--body-color);
}
.circle-dark{
    transform: translateX(20px);
    background-color: var(--circle-background);
}

@media screen and(max-width: 1150px){
    .mode-toggle{
        position: absolute;
        top: 15px;
        right: 15px;
    }
}
