.lang-toggle{
    position: fixed;
    z-index: 15;
    top: 16px;
    left: 10px;
    max-width: 60px;
    animation: image-appear 1s ease;

    &,
    *{
        user-select: none;
        -webkit-tap-highlight-color: transparent;
    }

    .all-langs{
        display: none;
        position: absolute;
        box-shadow: 0 10px 15px 5px rgba(0,0,0,.05), 0 10px 15px 5px rgba(0,0,0,.05);
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        background-color: var(--body-bg-color);
    }
    img{
        cursor: pointer;
        display: block;
        padding: 5px 15px;
        
        &:last-child{
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
        }
        &:hover{
            background-color: var(--border-color);
        }
        .active-lang:hover{
            background-color: initial;
        }
    }
}
@media screen and(max-width: 1150px){
    .lang-toggle{
        position: absolute;
        top: 6px;
        left: 0;
    }
}