
.cookie-consent{
    display: flex;
    position: fixed;
    bottom: 2.5%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;
    margin: auto;
    padding: 15px 0;
    width: 840px;
    border: 1px solid var(--border-color);
    border-radius: 5px;
    box-shadow: 0 10px 15px 5px rgba(0,0,0,.05), 0 10px 15px 5px rgba(0,0,0,.05);
    background-color: var(--title-bg-color);
    transition: width 100ms ease;
    animation: image-appear 1s ease;

    img{
        margin: 0 15px;
        width: 50px;
        height: 55px;
        user-select: none;
        -webkit-tap-highlight-color: transparent;
    }
    span{
        display: inline-block;
        padding-right: 15px;
        border-right: 1px solid var(--border-color);
        line-height: 1.5;
        font-size: 18px;
        color: var(--title-color);
    }
    .close{
        cursor: pointer;
        position: relative;
        margin-left: 25px;
        margin-right: 25px;
        width: 50px;
        height: 50px;
        font-size: 40px;
        text-align: center;
        justify-content: center;
        align-self: center;
        color: var(--p-color);
        transition: 0.25s;
        user-select: none;
        -webkit-tap-highlight-color: transparent;

        &:hover{
            color: var(--highlight-color);
        }
    }
}

@media screen and(max-width: 1150px){
    .cookie-consent{
        left: 5%;
        width: 90%;
        transform: none;

        span{
            font-size: 15px;
        }
    }
}

@media screen and (max-width: 550px){
    .cookie-consent{
        left: 2.5%;
        width: 95%;

        span{
            font-size: 12px;
        }
        .close{
            margin-left: 15px;
            margin-right: 15px;
            width: 40px;
            height: 40px;
            font-size: 30px;
        }
    }
}